.home {
    section {
        margin-bottom: 3rem;
        @include media( $desktop-width ) {
            display: none;
        }
    }
    section.desktop {
        display: none;
        @include media( $desktop-width ) {
            display: block;
            margin: 0;
        }
    }
    .season-pass {
        .season-image-container img {
            width: 100%;
        }
        .season-copy {
            text-align: center;
            margin: 1rem 0;
            p, h2 {
                margin: 0;
            }
            p {
                font-size: 1.5rem;
                font-weight: 300;
            }
        }
    }
    .concert {
        .concert-copy {
            padding: 0 1rem;
            p {
                margin-bottom: .5rem;
            }
            .concert-excerpt {
                font-weight: 300;
                margin: 0;
            }
        }
        #concert-carousel {
            margin-top: 2rem;
            .owl-dots {
                position: absolute;
                top: -2rem;
                left: 0;
                right: 0;
            }
            .owl-nav {
                position: absolute;
                top: -3.3rem;
                display: flex;
                justify-content: space-between;
                width: 100%;
            }
            .owl-prev,
            .owl-next {
                background: none;
                color: #000;
                opacity: 1;
                z-index: 1;
            }
            .concert-post-head {
                position: relative;
                height: 200px;
                display: flex;
                justify-content: center;
                align-items: center;
                overflow: hidden;
                @include media( $mobile-large ){
                    height: 400px;
                }
                img {
                    position: absolute;
                    width: 100%;
                    max-width: none;
                    left: 0;
                }
                .concert-title {
                    position: absolute;
                    color: #fff;
                    top: 0;
                    left: 0;
                    font-size: 2rem;
                    margin: 1rem;
                    line-height: 1;
                }
                .concert-date {
                    position: absolute;
                    color: #fff;
                    bottom: 0;
                    left: 0;
                    font-size: 1.2rem;
                    font-weight: 300;
                    margin: 1rem;
                }
            }
        }
    }
    .blog {
        .blog-post {
            padding: 0 1rem;
        }
        .blog-image-container {
            margin: 0 auto;
            overflow: hidden;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
    .blog-title {
        margin-bottom: 0.5rem;
    }
    .blog-excerpt {
        font-weight: 300;
        margin: 0;

    }
    .buy-donate {
        display: flex;
        justify-content: space-around;
        margin: 3rem 0;
        .button {
            width: 40%;
            text-align: center;
            padding: .4rem .7rem;
        }
        @include media( $desktop-width ) {
            display: none;
        }
    }
    //desktop
    @include media ($desktop-width) {
        .slide {
            display: flex;
            width: 100%;
            height: 100%;
        }
        .left-column {
            flex: 1.5;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .right-column {
            flex: 1;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .circle-overlay {
            overflow: hidden;
            height: 100%;
            width: 100%;
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .carousel-img-container img {
            position: absolute;
        }
        .slide-content {
            padding: 0 4rem 0 2rem;
            h2 {
                text-align: left;
            }
        }
        .flickity-page-dots {
            width: initial;
            bottom: 55px;
            right: 16.5%;
        }
    }
}